.scrollup {
  position: fixed;
  right: 2.5rem;
  bottom: 20%;
  background-color: #f8f7f7;
  opacity: 0.8;
  padding: 0.3rem 0.5rem;
  border-radius: 0.4rem;
  z-index: var(--z-tooltip);
  transition: 0.4s;
}
.scrollup__icon {
  font-size: 1rem;
  color: #000000;
}

.scrollup__hover {
  background-color: var(--title-color-dark);
}

/*show scroll*/
.show-scroll {
  bottom: 3rem;
}
