/*===================GOOGLE FONTS==============*/
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;800&family=Poppins:wght@400;500;600&display=swap");

:root {
  --header-hight: 3rem;

  /*========COLORS=========*/
  /*Color mode HSL(HUE, SATURATION, LIGHTNESS)*/
  --hue: 0;
  --sat: 0;
  --title-color: hsl(var(--hue), var(--sat), 20%);
  --title-color-dark: hsl(var(--hue), var(--sat), 0%);
  --text-color: hsl(var(--hue), var(--sat), 46%);
  --body-color: #f8f7f7;
  --container-color: #fff;

  /*=============Font and typography==========*/
  --body-font: "Poppins", sans-serif;

  /* .5rem = 8px | 1rem = 16px...*/
  --big-font-size: 3rem;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  --tiny-font-size: 0.625rem;

  /*======== Font weight ======*/
  --font-normal: 400;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========= Margenes Bottom ========*/
  /* .5rem = 8px | 1rem = 16px...*/
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /*============z index============*/
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/*Responsive typography*/
@media screen and (max-width: 992px) {
  :root {
    --big-font-size: 2rem;
    --h1-font-size: 1rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/*======== BASE ========*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*======== REUSEABLE CSS CLASSES ========*/
.selection {
  padding: 6rem 0 2rem;
}

.section__title {
  margin-top: 118px;
  font-size: var(--big-font-size);
  color: var(--title-color);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: 4rem;
  font-weight: 600;
  color: #5e5d5d;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/*======== lAYOUT ========*/
.container {
  max-width: 960px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

/*======== BUTTONS ========*/
.button {
  display: inline-block;
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius: 3.375rem;
  font-weight: var(--font-medium);
}

.button__icon {
  margin-left: var(--mb-0-5);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}
/*======== BERAKPOINTS ========*/
/*For large devices*/
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
}

/*For medium devices*/
@media screen and (max-width: 768px) {
}

@media screen and (max-width: 576px) {
}

/*For small devices*/
@media screen and (max-width: 350px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
}
